@import "variables.scss";

@media (min-width: 576px) {
  body {
    font-size: 1.5rem;

    .queries-sm {
      display: block;
      font-size: 1rem;
    }

    .queries-xsm {
      display: none;
    }

    header {
      flex-direction: row;
      justify-content: space-between;

      nav {
        width: 100%;

        .logo {
          margin-left: 10%;
        }
      }

      button {
        align-self: baseline;
        margin-right: 2%;
      }
    }
  }

  #first-section {
    article {
      font-size: 0.8rem;
      left: 10%;
      top: 25%;

      img {
        width: 30vw;
        margin-bottom: 7%;
      }

      button {
        margin-top: 4%;
        padding: 4% 15%;
      }
    }

    .adresses {
      bottom: 0%;
    }
  }

  #second-section {
    article {
      div {
        right: -50%;
        top: 100%;
      }
    }
  }

  #fourth-section {
    button {
      padding: 4% 30%;
      background-color: $green;
      border: none;
      color: $off-white;
      text-transform: uppercase;
      font-size: 1rem;
      min-width: 250px;
    }
  }
}

@media (min-width: 768px) {

  /* sticky nav
       sliders
    /* phone at first section and none footer*/
  body {
    .queries-md {
      display: block;
      font-size: 1rem;
    }

    .queries-xsm,
    .queries-sm {
      display: none;
    }

    font-size: 1.3rem;
  }

  header {
    nav {
      .logo {
        width: 15vw;
      }
    }
  }

  #first-section {
    margin-top: 0%;

    article {
      top: 20%;
      left: 15%;
    }

    .adresses {
      right: 0px;
      bottom: 5%;
      color: $off-white;
      text-shadow: none;
    }

    .arrow-left {
      display: block;
      top: 45%;
      left: -20%;
      background-color: $grey;
      padding: 1% 2%;
      transform: rotate(-180deg);
      width: 2vw;
    }

    .arrow-right {
      display: block;
      top: 45%;
      right: -65%;
      background-color: $grey;
      padding: 1% 2%;
      width: 2vw;

    }
  }

  #second-section {
    margin: 0% 10%;
    font-size: 1rem;
    overflow: visible;

    article {
      top: -0%;
      left: 3%;
      position: relative;
    }

    .woman-in-room {
      position: absolute;
      left: -6%;
      width: 71%;
    }

    .litle-pic {
      width: 53%;
      right: -3%;
      height: 29%;
      top: 32%;
      object-fit: cover;
      object-position: center;
    }
  }

  #third-section {
    overflow: visible;
    margin-top: -10%;

    .room-picture {
      left: 50%;
      top: 5%;
      width: 38%;
      height: 107%;
      object-fit: cover;
      object-position: revert;
    }

    .chill-picture {
      width: 53%;
      top: 45%;
    }

    article {
      left: -35%;
      top: 5%;

      h2 {
        text-align: center;
      }

      h3,
      h4,
      h5 {
        text-align: center;
        font-size: 1rem;
        line-height: 0;
        margin: 10px;
      }

      div {
        top: 100%;
        left: 32%;
        color: $black;
        text-shadow: none;
        text-align: center;
        font-size: 1rem;
      }
    }
  }

  footer {
    .logo {
      margin-left: auto;
      width: 15vw;
    }
  }
}

@media (min-width: 992px) {
  body {
    .queries-lg {
      display: block;
    }

    .queries-xsm,
    .queries-sm,
    .queries-md {
      display: none;
    }

    font-size: 1rem;
  }

  header {
    height: 8vw;
    position: fixed;
    top: 0;
    z-index: 2;
    width: 100%;
    background-color: $off-white;

    nav {
      .logo {
        width: 10vw;
      }
    }
  }

  #first-section {}

  #second-section {}

  #third-section {}

  #fourth-section {
    background-color: $off-white;
    align-self: center;
    display: flex;

    section {
      img {
        width: 60%;
        position: sticky;
        top: 4%;
        left: 20%;
        margin: auto;
      }

      main {
        h2 {
          height: 50px;
        }

        article {
          justify-content: space-around;

          ul {
            li {
              font-size: 1rem;
            }
          }
        }

        button {
          margin-top: 50px;
          padding: 5%;
        }
      }
    }

    .card-description {
      min-height: 100px;
      font-size: 1.2rem;
    }

    .card-size {
      height: 115px;
    }
  }
}

@media (min-width: 1200px) {
  body {

    .queries-xsm,
    .queries-sm,
    .queries-md,
    .queries-lg {
      display: none;
    }

    .queries-xl {
      display: block;
    }

    font-size: 1.5rem;
  }
}